@import url('https://fonts.googleapis.com/css2?family=Inter:wght@100;300;400;500&family=Overpass:wght@200;300&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Dosis:wght@500&display=swap');

.p {
    font-size: 1.55rem;
    font-family: Overpass;
    font-style: normal;
    font-weight: 200;
}

.card-desc {
    font-size: 1.2rem;
    font-family: Overpass;
    font-style: normal;
    font-weight: 300;
}

.b-300 {
    font-family: Inter;
    font-weight: 500;
}

.border-0 {
    border: none;
}

.progress {
    display: flex;
    height: 0.3rem;
    overflow: hidden;
    font-size: 0.75rem;
    background-color: #eaf0fc;
    border-radius: 0.25rem;
}

h4,
h6 {
    margin-bottom: 0.5rem;
    font-family: "Dosis", sans-serif;
    font-weight: 500;
    line-height: 1.2;
}

.text-white-50 {
    color: rgba(255, 255, 255, 0.8) !important;
}

.title {
    font-size: calc(15px + (20 - 15) * ((100vw - 300px) / (1300 - 300)));
}

.sub-title {
    font-size: calc(13px + (14 - 13) * ((100vw - 300px) / (1300 - 300)));
    opacity: .8;
}

.line {
    display: block;
    width: 25px;
    height: 1.5px;
}

ion-icon {
    width: 20px;
    height: 20px;
    padding: 0 10px 0 0;
}

@media(max-width:720px) {
    .container-sm {
        max-width: 426px;
    }
}
