@import url('https://fonts.googleapis.com/css2?family=Inter:wght@100;300;400;500&family=Overpass:wght@200&display=swap');
@import url("https://fonts.googleapis.com/css2?family=Montserrat&display=swap");

* {
    box-sizing: border-box;
}

.navbar-brand {
    text-align: center;
    font-family: Inter;
    font-size: 2rem;
    font-style: italic;
    font-weight: 400;
    line-height: normal;
    pointer-events: none;
    /* transition: all 0.5s ease-in; */
}

@media (min-width: 992px) {
    .navbar-expand-lg .navbar-nav {
        margin-right: 50px;
        flex-direction: row;
        justify-content: space-between;
    }
}

.navbar-expand-lg .navbar-nav .nav-link {
    font-size: 1.2rem;
    font-weight: 350;
    margin: 0px 10px;
    padding: auto;
    transition: all .2s ease-in;
}

.navbar-expand-lg .navbar-nav .nav-link:hover {
    font-weight: 400;
    text-decoration: underline;
}

.checkbox {
    opacity: 0;
    position: absolute;
}

.checkbox-label {
    background-color: #111;
    width: 40px;
    height: 20px;
    border-radius: 50px;
    position: relative;
    padding: 5px;
    cursor: pointer;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.checkbox-label .ball {
    width: 15px;
    height: 15px;
    position: absolute;
    left: 2px;
    top: 2px;
    border-radius: 1px solid #fff;
    border-radius: 50%;
    transition: transform 0.2s linear;
}

.checkbox:checked+.checkbox-label .ball {
    transform: translateX(22px);
}

.imgContainer {
    height: 93vh;
    min-height: 600px;
    background-image: url(../Images/Saurav_Patil.jpg);
    background-attachment: scroll;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
}

.info {
    position: absolute;
    left: 0;
    top: 15%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 10vw 9vw;
}

@media(max-width:720px) {
    .imgContainer {
        height: 60vh;
    }

    .info {
        top: 35%;
    }
}

.info .greeting {
    color: #FDF6E4;
    font-size: calc(20px + (45 - 20) * ((100vw - 300px) / (1600 - 300)));
    font-family: Overpass;
    font-style: normal;
    font-weight: 700;
    transition: 0.5s ease-in;
}

.info .title {
    color: #F5F3FD;
    font-size: calc(12px + (19 - 12) * ((100vw - 300px) / (1600 - 300)));
    font-family: Overpass;
    font-style: normal;
    font-weight: 200;
    line-height: normal;
    text-transform: capitalize;
    transition: 0.5s ease-in;
}

.info .myName {
    color: #FBFCF8;
}

.printResume {
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50px;
    border: 1px solid #FBFCF8;
    color: #DEF2F1;
    font-family: Overpass;
}

.printResume a {
    text-decoration: none;
    color: #F8EEEC;
    padding: 1vw;
}

.printResume:hover {
    background-color: #F8EEEC;
    color: #0B0C10;
    border: #0B0C10;
    cursor: pointer;
}

.printResume a:hover {
    color: #0B0C10;
    text-decoration: underline;
}

/* style={{ background: props.BGColor === "#17252A" ? "rgb(222, 242, 241)" : "rgb(23, 37, 42)" }} */
.classEffect {
    background: rgba(222, 242, 241, 0.28);
    box-shadow: 0 4px 30px rgba(0, 0, 0, 0.2);
    backdrop-filter: blur(20px);
    -webkit-backdrop-filter: blur(20px);
    border: 1px solid rgba(253, 253, 253, 0.582);
}

.sticky {
    margin-bottom: 2vw;
    position: sticky;
    top: 0;
}

.circleImg {
    min-height: 350px;
    min-width: 350px;
    background-image: url(../Images/myImg.jpg);
    border-radius: 50%;
    background-position: center;
    background-size: cover;
}